.shampoo-capacity {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    margin-bottom: 15px;
}

.shampoo-capacity input {
    display: none;
}

.shampoo-capacity label {
    position: relative;
    padding-left: 30px;
    width: 100px;
    height: 22px;
    font-weight: 500;
    cursor: pointer;
}

.shampoo-capacity label::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 22px;
    height: 22px;
    background-color: #DEDEDE;
    border-radius: 5px;
}

.shampoo-capacity label::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 3px;
    transform: translateY(-50%);
    display: none;
    width: 17px;
    height: 17px;
    background-image: linear-gradient(90deg, #CF9E1F, #EFBB37);
    border-radius: 5px;
}

.shampoo-capacity input:checked + label::after {
    display: block;
}

