.shampoo-color {
    display: inline-flex;
    flex-direction: column;
    position: relative;
    width: 255px;
    height: 35px;
    margin-bottom: 15px;
    background-color: white;
    font-size: 12px;
    font-weight: 500;
    border-radius: 15px;
    z-index: 10;
}

.shampoo-color label:not(:first-of-type) {
    display: none;
}

.shampoo-color label.active {
    display: block;
}

.shampoo-color .select-title.active {
    display: flex;
    border-radius: 15px 15px 0 0;
}


.shampoo-color label {
    padding: 5px 20px;
    background-color: white;
    z-index: 5;
}

.shampoo-color label:not(:first-of-type):hover {
    background-color: #b5deff;
}

.select-title {
    min-height: 35px;
    display: flex;
    align-items: center;
    padding: 10px 20px;
    border-radius: 15px;
    cursor: pointer;
}


.shampoo-color .select-title::before {
    content: url("../../../img/arrow-icon.svg");
    position: absolute;
    right: 25px;
}
.shampoo-color label:last-child {
    border-radius: 0 0 15px 15px;
}

.shampoo-color input {
    display: none;
}