.shampoo-quantity {
    display: inline-flex;
    align-items: center;
    width: 90px;
    height: 40px;
    padding: 0 10px;
    margin-right: 35px;
    font-weight: 500;
    font-size: 19px;
    border-radius: 30px;
    background-color: #E2E9F0;
    transition: 0.3s;
}

.shampoo-quantity:hover {
    box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.2);
}

.shampoo-quantity button {
    flex-basis: 25%;
    font: inherit;
    cursor: pointer;
}
.shampoo-quantity output {
    flex-basis: 50%;
    text-align: center;
}