.shampoo-item {
    position: relative;
    width: 575px;
    padding: 50px 45px 37px 45px;
    background-color: #F8FAFC;
    border-radius: 25px;
    transition: 0.3s;
}

.shampoo-item:hover {
    box-shadow: 0 0 10px 6px rgba(68, 174, 255, 0.2);
}

.shampoo-img {
    display: block;
    margin: 0 auto 25px;
}

h3 {
    text-align: center;
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 10px;
}

.shampoo-descr {
    line-height: 1.5;
    margin-bottom: 20px;
}

.new-label {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 30px;
    width: 70px;
    height: 25px;
    background-image: linear-gradient(90deg, #CF9E1F, #EFBB37);
    color: white;
    font-size: 14px;
    border-radius: 30px;
}

.open-button {
    position: absolute;
    top: 50px;
    right: 30px;
    display: flex;
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-color: white;
    box-shadow: 0 0 8px 0 rgba(34, 60, 80, 0.2);
    cursor: pointer;
    transition: 0.3s;
}

.open-button:hover {
    box-shadow: 0 0 17px 6px rgba(68, 174, 255, 0.2);
}

.open-button::before {
    display: inline-block;
    content: url("../../img/weigher-icon.svg");
    margin: auto;
    width: 16px;
    height: 16px;
}

.shampoo-price {
    position: relative;
    left: 150px;
    font-weight: 600;
}

.submit-button {
    padding: 7px 20%;
    border-radius: 30px;
    background-image: linear-gradient(90deg, #CF9E1F, #EFBB37);
    color: white;
    text-transform: uppercase;
    font: inherit;
    font-weight: 500;
    transition: 0.3s;
    cursor: pointer;
}

.submit-button:hover {
    box-shadow: 0 0 15px 5px rgba(255, 169, 0, 0.4);
}

.submit-button:active {
    background-image: linear-gradient(90deg, #CF9E1F, #EFBB37);
}